<template>
  <div class="switch-container">
    <div class="switch" :class="{'active':isActive}" @click="toggle">
      <!-- : 콜론은, 데이터바인딩시 사용하는 디렉티브 -->
       <div class="toggle"></div>
    </div>
              
  <ToggleSwitch />
  </div>
</template>

<script>
 

export default {
        data(){
            return {
                isActive : false, // 초기상태 비활성화
            };
        },
        methods: {
            // 스위치 상태 반전 토글 메서드 
            toggle() {
                //  isActive 값 반전하여 활성상태로 변경
                this.isActive = !this.isActive;
            }
        }
    }

</script>

<style>
.switch-container {
  display: flex; 
  justify-content: center; 
  align-items: center; 
  height: 100vh; 
}
/* 스위치 자체의 스타일링 */
.switch {
  width: 60px; 
  height: 30px; 
  background-color: #d3d3d3; 
  border-radius: 20px; 
  position: relative; 
  cursor: pointer; 
  transition: background-color 0.3s; 
}
/* 스위치가 활성화되었을 때의 스타일 */
.switch.active {
  background-color: #4caf50; 
}
/* 스위치 안의 토글(버튼) 스타일링 */
.toggle {
  width: 24px; 
  height: 24px; 
  background-color: white; 
  border-radius: 50%; 
  position: absolute; 
  top: 3px; 
  left: 3px; 
  transition: left 0.3s; 
}
/* 스위치가 활성화 되었을 때 토글의 위치를 오른쪽으로 이동 */
.switch.active .toggle {
  left: 33px; 
}
</style>  